// Import the bold font
@font-face {
  font-family: 'TheBoldFont';
  src: url('../assets/fonts/THEBOLDFONT-FREEVERSION.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

// Import custom styles
@import '../styles/amount-input.scss';

// Core Animations
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes pulseBadge {
  0% {
    box-shadow: 0 0 0 0 rgba(54, 202, 186, 0.4);
  }
  70% {
    box-shadow: 0 0 0 6px rgba(54, 202, 186, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(54, 202, 186, 0);
  }
}

// Base Styles

// Main Container
.compact-presale {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  min-height: 100vh;
}

// Dashboard Layout
.presale-dashboard {
  animation: fadeIn 0.8s ease-out;
  position: relative;
  z-index: 2;
  max-width: 1200px;
  margin: 0 auto;
}

// Header Section with Total Raised
.header-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-bottom: 30px;

  .banner-mini {
    flex: 1;
    min-width: 280px;
    position: relative;
    background: url('../assets/banner.jpeg') center/cover;
    border-radius: 20px;
    border: 1px solid rgb(54, 202, 187);
    overflow: hidden;
    padding: 35px;
    min-height: 240px;
    
    // Enhanced dark overlay with gradient pattern - increased opacity
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgb(20, 25, 45);
      z-index: 1;
    }
    
   
    
    // Enhanced title with multiple effects
    h1 {
      font-size: 48px;
      color: #36CABA;
      margin: 0 0 30px 0;
      position: relative;
      z-index: 3;
      text-transform: uppercase;
      letter-spacing: 4px;
      text-align: center;
      font-weight: bold;
      
      // Multiple glow layers
      &::before {
        content: 'HashLordz Miner';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        color: rgba(54, 202, 186, 0.2);
        filter: blur(15px);
        z-index: -1;
      }
      
      &::after {
        content: 'HashLordz Miner';
        position: absolute;
        top: 2px;
        left: 2px;
        right: 0;
        color: rgba(42, 158, 142, 0.3);
        filter: blur(5px);
        z-index: -1;
      }
    }
    
    .phase-info {
      position: relative;
      z-index: 3;
      display: grid;
      grid-template-columns: 1fr;
      gap: 25px;

      .current-phase {
        background: 
          linear-gradient(135deg, rgba(20, 25, 45, 0.98), rgba(30, 35, 55, 0.95)),
          url('../assets/banner1.jpeg') center/cover;
        background-blend-mode: overlay;
        padding: 25px;
        border-radius: 18px;
        border: 3px solid rgb(54, 202, 187);
        background-clip: padding-box;
        backdrop-filter: blur(20px);
        box-shadow: 
          0 12px 35px rgba(0, 0, 0, 0.5),
          inset 0 2px 2px rgba(255, 255, 255, 0.1),
          0 0 0 1px rgba(54, 202, 186, 0.3);
        position: relative;
        overflow: hidden;
        
        // Corner accents
        &::after {
          content: '';
          position: absolute;
          top: 10px;
          right: 10px;
          width: 30px;
          height: 30px;
          background: linear-gradient(135deg, #36CABA, transparent);
          clip-path: polygon(0 0, 100% 0, 100% 100%);
          opacity: 0.3;
        }
        
        p {
          margin: 0;
          color: #ffffff;
          font-size: 20px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          gap: 20px;
          flex-wrap: wrap;
          font-weight: 700;
          text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
          
          // Enhanced phase label
          &::before {
            font-size: 14px;
            color: #36CABA;
            position: absolute;
            top: -12px;
            left: 20px;
            background: linear-gradient(135deg, rgba(20, 25, 45, 0.95), rgba(54, 202, 186, 0.2));
            padding: 6px 12px;
            border-radius: 6px;
            text-transform: uppercase;
            letter-spacing: 1px;
            border: 1px solid rgba(54, 202, 186, 0.3);
          }
        }
      }

      .phase-eligibility {
        background: 
          linear-gradient(135deg, rgba(20, 25, 45, 0.98), rgba(30, 35, 55, 0.95)),
          url('../assets/mission1.jpeg') center/cover;
        background-blend-mode: overlay;
        border-radius: 18px;
        padding: 25px;
        border: 3px solid rgb(54, 202, 187);
        backdrop-filter: blur(20px);
        box-shadow: 
          0 12px 35px rgba(0, 0, 0, 0.5),
          inset 0 2px 2px rgba(255, 255, 255, 0.1),
          0 0 0 1px rgba(54, 202, 186, 0.3);
        position: relative;
        overflow: hidden;

        // Subtle pattern overlay
        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: 
            radial-gradient(circle at 25% 25%, rgba(54, 202, 186, 0.05) 0%, transparent 50%),
            radial-gradient(circle at 75% 75%, rgba(54, 202, 186, 0.03) 0%, transparent 50%);
          z-index: 1;
        }

        .eligibility-label {
          display: block;
          color: #f1f1f1;
          font-size: 18px;
          margin-bottom: 20px;
          font-weight: bold;
          text-transform: uppercase;
          letter-spacing: 3px;
          text-align: center;
          text-shadow: 0 0 15px rgba(54, 202, 186, 0.5);
          position: relative;
          z-index: 2;
          
          &::after {
            content: '';
            position: absolute;
            bottom: -8px;
            left: 50%;
            transform: translateX(-50%);
            width: 60px;
            height: 2px;
            background: linear-gradient(90deg, transparent, #36CABA, transparent);
          }
        }

        .phase-badges {
          display: flex;
          gap: 20px;
          flex-wrap: wrap;
          justify-content: center;
          position: relative;
          z-index: 2;

          .phase-badge {
            padding: 15px 25px;
            border-radius: 30px;
            font-size: 16px;
            background: rgba(54, 202, 186, 0.1);
            color: #ffffff;
            border: 2px solid rgba(102, 102, 102, 0.3);
            transition: all 0.3s ease;
            display: flex;
            align-items: center;
            gap: 10px;
            white-space: nowrap;
            font-weight: 700;
            position: relative;
            overflow: hidden;
            backdrop-filter: blur(10px);

            .badge-icon {
              font-size: 20px;
              filter: drop-shadow(0 0 8px rgba(54, 202, 186, 0.4));
            }

            &.active {
              background: 
                linear-gradient(135deg, rgba(54, 202, 186, 0.3), rgba(42, 158, 142, 0.2)),
                rgba(54, 202, 186, 0.1);
              color: #ffffff;
              border-color: rgba(54, 202, 186, 0.8);
              transform: translateY(-3px) scale(1.05);
              box-shadow: 
                0 12px 25px rgba(54, 202, 186, 0.4),
                inset 0 2px 2px rgba(255, 255, 255, 0.2),
                0 0 30px rgba(54, 202, 186, 0.3);
              text-shadow: 0 0 10px rgba(54, 202, 186, 0.6);

              .badge-icon {
                filter: drop-shadow(0 0 12px rgba(54, 202, 186, 0.8));
              }
            }

            &:not(.active) {
              opacity: 0.6;
              transform: scale(0.95);
              .badge-icon {
                opacity: 0.3;
              }
            }
          }
        }
      }
    }
  }
}

// Total Raised Card
.total-raised-card {
  width: 100%;
  background: url('../assets/banner.jpeg') center/cover;
  border: 2px solid #36CABA;
  border-radius: 15px;
  padding: 25px;
  position: relative;
  overflow: hidden;
  min-height: 200px; // Match banner height

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
      135deg,
      rgba(0, 0, 0, 0.85) 0%,
      rgba(20, 25, 45, 0.95) 50%,
      rgba(0, 0, 0, 0.8) 100%
    );
    z-index: 1;
  }

  // Top border gradient
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg, transparent, #36CABA, transparent);
    z-index: 2;
  }

  .raised-content {
    position: relative;
    z-index: 3;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .raised-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    
    .header-left {
      display: flex;
      align-items: center;
      gap: 10px;
      
      .raised-icon {
        font-size: 28px;
        filter: drop-shadow(0 0 8px rgba(54, 202, 186, 0.5));
      }
      
      .header-text {
        h3 {
          color: #36CABA;
          margin: 0;
          font-size: 18px;
          font-weight: bold;
          text-transform: uppercase;
          letter-spacing: 1px;
          text-shadow: 0 0 10px rgba(54, 202, 186, 0.3);
        }
        
        .subtitle {
          color: #ffffff;
          font-size: 12px;
          margin: 2px 0 0 0;
          text-transform: uppercase;
          text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.8);
        }
      }
    }

    .status-badge {
      background: rgba(54, 202, 186, 0.3);
      color: #36CABA;
      padding: 6px 12px;
      border-radius: 20px;
      font-size: 12px;
      font-weight: bold;
      border: 1px solid rgba(54, 202, 186, 0.5);
      animation: pulse 2s infinite;
      backdrop-filter: blur(10px);
      box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
    }
  }

  .raised-stats {
    margin-bottom: 20px;
    
    .main-amount {
      margin-bottom: 15px;
      
      .amount {
        font-size: 36px;
        color: #36CABA;
        font-weight: bold;
        text-shadow: 0 0 10px rgba(54, 202, 186, 0.3);
        display: block;
        line-height: 1;
      }
      
      .target {
        color: #a0a8c0;
        font-size: 16px;
        margin-top: 5px;
        display: block;
      }
    }

    .progress-section {
      margin-bottom: 15px;
      
      .progress-bar-full {
        width: 100%;
        height: 8px;
        background: rgba(54, 202, 186, 0.1);
        border-radius: 4px;
        overflow: hidden;
        position: relative;

        .progress-bar-full {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          // width: 57%; // Match the percentage
          background: linear-gradient(90deg, #36CABA, #2A9E8E);
          border-radius: 4px;
          animation: shimmer 2s infinite;
        }
      }

      .progress-text {
        display: flex;
        justify-content: space-between;
        margin-top: 8px;
        font-size: 12px;
        
        .percentage {
          color: #36CABA;
          font-weight: bold;
        }
        
        .remaining {
          color: #a0a8c0;
        }
      }
    }

    .mini-stats {
      display: flex;
      justify-content: center;

      .mini-stat {
        background: rgba(54, 202, 186, 0.1);
        padding: 15px 25px;
        border-radius: 12px;
        border: 1px solid rgba(54, 202, 186, 0.2);
        text-align: center;
        min-width: 150px;

        .value {
          color: #36CABA;
          font-size: 20px;
          font-weight: bold;
          display: block;
        }

        .label {
          color: #a0a8c0;
          font-size: 12px;
          text-transform: uppercase;
          margin-top: 4px;
        }
      }
    }
  }
}

@keyframes shimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-4px); }
  60% { transform: translateY(-2px); }
}

// Dashboard Grid
.dashboard-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-top: 30px;
}

// Info Column
.info-column {
  .quick-stats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 15px;
    margin-bottom: 20px;

    .stat {
      background: rgba(20, 25, 45, 0.9);
      border: 1px solid rgba(54, 202, 186, 0.3);
      border-radius: 10px;
      padding: 15px;
      
      .stat-label {
        display: block;
        color: #a0a8c0;
        font-size: 14px;
        margin-bottom: 5px;
      }
      
      .stat-value {
        display: block;
        color: #36CABA;
        font-size: 20px;
        font-weight: bold;
      }
    }
  }

  .balances-compact {
    background: rgba(20, 25, 45, 0.9);
    border: 1px solid rgba(54, 202, 186, 0.3);
    border-radius: 10px;
    padding: 20px;

    h4 {
      color: #36CABA;
      margin: 0 0 15px 0;
      font-size: 16px;
    }

    .balance-row {
      display: flex;
      justify-content: space-between;
      
      span {
        color: #a0a8c0;
        font-size: 16px;
        
        strong {
          color: #36CABA;
          margin-left: 5px;
        }
      }
    }
  }
}

// Action Column
.action-column {
  .action-card {
    background: rgba(20, 25, 45, 0.9);
    border: 2px solid #36CABA;
    border-radius: 15px;
    padding: 25px;

    h3 {
      color: #36CABA;
      margin: 0 0 20px 0;
      font-size: 20px;
      text-align: center;
    }
  }

  .purchase-area {
    margin-bottom: 25px;
    padding-bottom: 25px;
    border-bottom: 1px solid rgba(54, 202, 186, 0.3);

    .input-row {
      display: flex;
      gap: 10px;
      margin-bottom: 15px;

      .amount-input {
        flex: 1;
        background: rgba(15, 20, 40, 0.8);
        border: 2px solid #36CABA;
        border-radius: 8px;
        color: white;
        padding: 12px;
        font-size: 16px;

        &:focus {
          outline: none;
          border-color: #2A9E8E;
        }
      }

      .max-btn {
        padding: 0 20px;
        background: #36CABA;
        color: #0a0a1a;
        border: none;
        border-radius: 8px;
        font-weight: bold;
        cursor: pointer;
        
        &:hover {
          background: #2A9E8E;
        }
      }
    }

    .limits-info {
      display: flex;
      justify-content: space-between;
      margin-bottom: 15px;
      
      span {
        color: #a0a8c0;
        font-size: 14px;
      }
    }

    .progress-mini {
      height: 4px;
      background: rgba(54, 202, 186, 0.1);
      border-radius: 2px;
      margin-bottom: 20px;
      overflow: hidden;

      .progress-fill {
        height: 100%;
        background: #36CABA;
        transition: width 0.3s ease;
      }
    }
  }

  .claim-area {
    .claim-info {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;
      margin-bottom: 20px;
      
      span {
        color: #a0a8c0;
        
        &.status-ready {
          color: #36CABA;
        }
        
        &.status-waiting {
          color: #a0a8c0;
        }
      }
    }
  }
}

// Action Buttons
.action-btn {
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'TheBoldFont', sans-serif;

  &.purchase {
    background: #36CABA;
    color: #0a0a1a;
    
    &:hover:not(.disabled) {
      background: #2A9E8E;
      transform: translateY(-2px);
    }
  }

  &.claim {
    background: #36CABA;
    color: #0a0a1a;
    
    &:hover:not(.disabled) {
      background: #2A9E8E;
      transform: translateY(-2px);
    }
  }

  &.disabled {
    background: #666;
    cursor: not-allowed;
    opacity: 0.6;
  }
}

// Floating Coins
.floating-coins-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;

  .floating-coin {
    position: absolute;
    animation: float 6s ease-in-out infinite;
    opacity: 0.3;

    img {
      width: 30px;
      height: 30px;
    }

    &.coin-1 { top: 15%; left: 15%; animation-delay: 0s; }
    &.coin-2 { top: 25%; right: 15%; animation-delay: 1.5s; }
    &.coin-3 { bottom: 25%; left: 20%; animation-delay: 3s; }
    &.coin-4 { bottom: 20%; right: 20%; animation-delay: 4.5s; }
  }
}

// Success Modal
.success-modal-overlay {
  .success-card {
    background: rgba(20, 25, 45, 0.95) !important;
    border: 2px solid #36CABA !important;

    h2 {
      color: #36CABA !important;
    }

    button {
      background: #36CABA !important;
      color: #0a0a1a !important;

      &:hover {
        background: #2A9E8E !important;
      }
    }
  }
}

// Responsive Design
@media (max-width: 1200px) {
  .header-section {
    grid-template-columns: 1fr 350px;
  }
}

@media (max-width: 992px) {
  .header-section,
  .dashboard-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .connect-section {
    padding: 30px 20px;
    
    h1 {
      font-size: 28px;
    }
    
    p {
      font-size: 16px;
    }
    
    .info-items {
      flex-direction: column;
      align-items: center;
      
      span {
        width: 100%;
        text-align: center;
      }
    }
  }
}

@media (max-width: 768px) {
  .header-section {
    flex-direction: column;
    
    .banner-mini {
      text-align: center;
    }
    
    .total-raised-card {
      width: 100%;
    }
  }

  .dashboard-grid {
    grid-template-columns: 1fr;
  }

  .quick-stats {
    grid-template-columns: 1fr !important;
  }

  .balance-row {
    flex-direction: column;
    gap: 10px;
    text-align: center;
  }

  .input-row {
    flex-direction: column;
    
    .max-btn {
      width: 100%;
      padding: 12px !important;
    }
  }
}

// Enhanced Non-Connected State
.simple-presale {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  
}

.simple-content {
  width: 100%;
  max-width: 500px;
  text-align: center;
}

.connect-section {
  background: url('../assets/banner.jpeg') center/cover;
  border: 2px solid #36CABA;
  border-radius: 15px;
  padding: 40px;
  backdrop-filter: blur(10px);
  position: relative;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 13px;
    z-index: 1;
  }
  
  h1, p, .connect-btn {
    position: relative;
    z-index: 2;
  }
  
  h1 {
    font-size: 36px;
    color: #36CABA;
    margin: 0 0 15px 0;
    font-family: 'TheBoldFont', sans-serif;
  }
  
  p {
    color: #a0a8c0;
    font-size: 18px;
    margin: 0 0 30px 0;
  }
  
  .connect-btn {
    width: 100%;
    background: #36CABA;
    color: #0a0a1a;
    border: none;
    border-radius: 10px;
    padding: 15px 30px;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    transition: background 0.3s ease;
    font-family: 'TheBoldFont', sans-serif;
    
    &:hover {
      background: #2A9E8E;
    }
  }
}
.scrollbar-hide::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Opera */
}
.scrollbar-hide {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;     /* Firefox */
}