// Professional Amount Input Styling
.amount-input-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  
  .input-wrapper {
    display: flex;
    align-items: center;
    background: rgba(30, 30, 47, 0.8);
    border: 1px solid rgba(54, 202, 186, 0.3);
    border-radius: 12px;
    padding: 0.2rem;
    margin-bottom: 0.8rem;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease;
    
    &:focus-within {
      border-color: rgba(54, 202, 186, 0.8);
      box-shadow: 0 4px 15px rgba(54, 202, 186, 0.3);
    }
    
    .currency-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      background: linear-gradient(135deg, #36caba, #2a9e8e);
      color: white;
      font-family: 'TheBoldFont', sans-serif;
      font-size: 0.9rem;
      font-weight: bold;
      height: 35px;
      min-width: 45px;
      border-radius: 8px;
      margin-right: 0.5rem;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    }
    
    .deposit-input {
      flex: 1;
      background: transparent;
      border: none;
      color: white;
      font-family: 'TheBoldFont', sans-serif;
      font-size: 1.1rem;
      padding: 0.5rem;
      outline: none;
      
      &::placeholder {
        color: rgba(255, 255, 255, 0.4);
      }
      
      &::-webkit-inner-spin-button,
      &::-webkit-outer-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
    }
  }
  
  .amount-range {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 0.5rem;
    
    .range-label {
      font-size: 0.8rem;
      color: rgba(255, 255, 255, 0.6);
      
      span {
        color: #36caba;
        font-weight: bold;
      }
    }
    
    .range-slider {
      flex: 1;
      height: 6px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 3px;
      margin: 0 0.8rem;
      position: relative;
      overflow: hidden;
      
      .slider-track {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(255, 255, 255, 0.1);
      }
      
      .slider-progress {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        background: linear-gradient(90deg, #36caba, #2a9e8e);
        border-radius: 3px;
        transition: width 0.3s ease;
      }
    }
  }
}

.deposit-amount {
  font-family: 'TheBoldFont', sans-serif;
  font-size: 1.2rem;
  color: #36caba;
  font-weight: bold;
  
  &::after {
    content: ' tokens';
    font-size: 0.9rem;
    color: rgba(255, 255, 255, 0.6);
    margin-left: 0.3rem;
  }
}
